  manageGallery() : void
  {

     
    this.authService.getReglibreSext2(this.no_orientacion + '/resultado','estudiantepcb').then((data) => {
      var rr:any=[];
      rr=data;
      var encuentra=0;
      for(var j=0;j<rr.length;j++)       { if(rr[j].ANIO_EVALUACION=='2020' && rr[j].NO_OPORTUNIDAD==4)                    {        encuentra=1;               }                 }

      if(encuentra==1)   
      {
       this.msj.showError3('Importante','Ya aplicaste a la 4ta. fecha PCB, nuevas fechas a partir de mayo 2020.');
      return;
      
      }
      else{
   
        this.manageGallery22a();
      }
      


     

       
     }, (err) => {
      if(err.error=='Resultados no existen')
      {

       this.manageGallery22a();

      }
      else{
       this.msj.showError3('Mensaje','En este momento no es posible realizar asignación , intente mas tarde.');
    
       return;
      }

     });


  }
  manageGallery22a() : void
  {

   
    
    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
    
      return;
    }    
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
     
      return;
    }

    if(this.otrosid=='')
    {
      this.msj.showError3('Mensaje','No se puede asignar a esta unidad academica , salones insuficientes');
      
      return;
    }

    if(!this.idperiodo2[1])
    {
      this.msj.showError3('Mensaje','Seleccione la unidad academica , debido a que no se pudo cargar el periodo');
    
      return;
    }

 

    if(!this.form.controls['no_orientacion'].value)
    {
      this.msj.showError3('Mensaje','Ingrese un No. orientación / Carné  valido');
    
      return;
    }

    if(this.form.controls['no_orientacion'].value=='')
    {
      this.msj.showError3('Mensaje','Ingrese un No. orientación / Carné  valido');
    
      return;
    }



    let alert = this.alertCtrl.create({ title: this.nombre + ', ¿Asignar?',
    buttons: [
      {text: 'Si',
        handler: () => { 

    const loading = this.loadingCtrl.create({
      spinner: 'bubbles',
      content: "Espere un momento,  Realizando  Operación ......"
    });
    loading.present();

                  if(this.nombre=='')
                  {
                    this.msj.showError3('Error','Ingrese un NOV / carné valido');
                    loading.dismiss();
                      

                  }
                  else
                  {

                    this.authService.getReglibreSext2(this.form.controls['no_orientacion'].value + '/resultado','estudiantepcb').then((data1) => {
                
                    
                    var cad=[]
                    var data:any=[]
                    data=data1;
                      for(let i = 0; i < data.length; i++){
                        if(data[i].APROBACION=='1')
                        {
                          if(data[i].ID_MATERIA=='1'){cad.push({idmateria:'Biologia'})}
                          if(data[i].ID_MATERIA=='2'){cad.push({idmateria:'Fisica'})}
                          if(data[i].ID_MATERIA=='3'){cad.push({idmateria:'Lenguaje'})}
                          if(data[i].ID_MATERIA=='4'){cad.push({idmateria:'Matematica'})}
                          if(data[i].ID_MATERIA=='5'){cad.push({idmateria:'Quimica'})}
                        
                        }
                      }
                      
                    

                      this.resultado=cad
                      this.manageGallery2( loading)
                      
                    }, (err) => {
                      if(err.error=='Resultados no existen')
                      {
                       var cad=[]
                       this.resultado=cad
                                         this.manageGallery2( loading)
                   
                      }
                      else{
                   
                   
                       this.msj.showError3('Error','No se puede ralizar asignación en este momento');
                   
                       loading.dismiss();
                      }

                  
                  });

                  }

                   
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();

  }

  
